import axios from "axios";

const state = {
  items: [],
  group: [],
};

const getters = {
  getAllItems: (state) => state.items,
  getGroupItems: (state) => state.group,
};

const actions = {
  async act_requestAllItems({ commit }) {
    try {
      const itemsFound = await axios.get(
        `${process.env.VUE_APP_BACKEND}/items`
      );
      commit("SET_ALL_ITEMS", itemsFound.data);
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  },

  async act_get_available_items({ commit }) {
    try {
      const itemsFound = await axios.get(
        `${process.env.VUE_APP_BACKEND}/items/available`
      );

      commit("SET_ALL_ITEMS", itemsFound.data);
    } catch (error) {
      console.log(error);
    }
  },
  async act_addNewItem({ commit }, item) {
    try {
      const insertedItem = await axios.post(
        `${process.env.VUE_APP_BACKEND}/items`,
        item
      );

      if (!insertedItem.data) throw "duplicate";
      commit("newItem", insertedItem.data);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },
  async act_removeOne({ commit, dispatch }, id) {
    try {
      const isDeleted = await axios.delete(
        `${process.env.VUE_APP_BACKEND}/items/${id}`
      );
      if (!isDeleted) throw "there some problem";
      commit("deleteItem", id);
      dispatch("rounds/act_get_all_rounds", {
        root: true,
      });
      return true;
    } catch (error) {
      console.log(error);
    }
  },
  async act_search({ commit }, number) {
    try {
      const itemsFound = await axios.get(
        `${process.env.VUE_APP_BACKEND}/items/search?item_no=${number}`
      );
      if (!itemsFound) return false;
      commit("SET_ALL_ITEMS", itemsFound.data);
      return true;
    } catch (error) {
      console.log(error);
    }
  },

  async act_remove_item_from_items({ commit }, id) {
    try {
      commit("deleteItem", id);

      return true;
    } catch (error) {
      console.log(error);
    }
  },

  async act_return_item_into_items({ commit }, item) {
    try {
      commit("newItem", item);

      return true;
    } catch (error) {
      console.log(error);
    }
  },

  /**Grouping */
  async act_add_item_to_group({ commit }, id) {
    try {
      const foundItem = await axios.get(
        `${process.env.VUE_APP_BACKEND}/items?id=${id}`
      );

      commit("add_item_to_group", foundItem.data[0]);
      return true;
    } catch (error) {
      console.log(error);
      return error;
    }
  },
  act_rm_item_from_group({ commit }, id) {
    commit("remove_item_from_group", id);
  },

  act_rm_all_from_group({ commit, dispatch }) {
    commit("remove_all_items_from_group");
    dispatch(
      "act_requestAllItems",
      {},
      {
        root: true,
      }
    );
  },
};

const mutations = {
  SET_ALL_ITEMS: (state, data) => (state.items = data),
  newItem: (state, data) => state.items.push(data),
  deleteItem: (state, id) =>
    (state.items = state.items.filter((item) => item.id != id)),
  search: (state, number) =>
    (state.items = state.items.filter((item) => item.item_no === number)),

  /**Grouping */
  add_item_to_group: (state, data) => {
    state.group.push(data);
    state.items = state.items.filter((itm) => itm.id != data.id);
  },
  remove_item_from_group: (state, id) => {
    const found = state.group.find((itm) => itm.id === id);
    state.items.push(found);
    state.group = state.group.filter((item) => item.id != id);
  },
  remove_all_items_from_group: (state) => (state.group = []),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
