<template>
  <input
    v-if="type === 'file'"
    :name="name"
    :type="type"
    accept=".csv"
    @change="$emit('getFiles', $event.target.files)"
    :placeholder="placeholder"
  />
  <input
    v-else-if="type === 'number'"
    :min="min"
    :max="max"
    :step="step"
    :name="name"
    :type="type"
    @input="$emit('update:modelValue', $event.target.value)"
    @change="$emit('onChange', $event.target.value)"
    :placeholder="placeholder"
  />
  <input
    v-else
    :name="name"
    :type="type"
    @input="$emit('update:modelValue', $event.target.value)"
    @change="$emit('onChange')"
    :placeholder="placeholder"
  />
</template>

<script>
export default {
  name: "InputField",

  props: {
    type: String,
    placeholder: String,
    name: String,
    min: Number,
    max: Number,
    step: String,
  },
};
</script>

<style lang="scss" scoped>
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"],
input[type="text"],
input[type="file"] {
  border: none;
  border: 1px solid $line-lighter;
  border-radius: 10px;
  padding: 10px;
  outline: 0;
  width: auto;
  float: left;
  font-family: inherit;
}

input {
  font-weight: 700;
  color: $dark-text;
  font-size: 1rem;
}
:hover {
  box-shadow: 0 0 0 1pt $secondary;
}
::placeholder {
  font-size: 0.85rem;
  color: $hint-text;
  font-weight: 400;
}
</style>
