<template>
  <form @submit.prevent="saveItem" class="slideForm">
    <div class="form-title">
      {{ lang.addItem }}
    </div>
    <div class="hint">
      {{ lang.addItemHint }}
    </div>
    <div
      class="inputGroup"
      v-for="(field, key) in Object.keys(item)"
      :key="key"
    >
      <label>{{ lang[field] }}</label>
      <InputField
        type="text"
        v-model="item[field]"
        :value="item[field]"
        :placeholder="lang[field]"
      />
    </div>
    <div class="buttons-bar">
      <Button type="submit" :text="lang.save" icon="save" />
      <Button color="red" :text="lang.cancel" icon="cancel" @onClick="cancel" />
    </div>
  </form>
</template>

<script>
import { mapActions } from "vuex";
import Button from "../../buttons/Button.vue";
import InputField from "../../inputs/InputField.vue";

export default {
  name: "AddBuyerForm",
  components: { InputField, Button },
  data() {
    return {
      item: {},
    };
  },
  methods: {
    ...mapActions(["act_add_buyer"]),
    async saveItem() {
      const buyer = this.item;
      try {
        if ((buyer.sign_no || buyer.fullname) === "") {
          return this.$toast.error(this.lang.fillRequired);
        }
        const isInserted = await this.act_add_buyer(buyer);
        //FIXME: LANGUAGE
        if (!isInserted)
          throw this.$toast.error(
            this.lang.failedProcess + " ربما تكون القيم مدخلة مسبقاً"
          );
        this.$toast.success(this.lang.successProcess);
        this.cancel();
      } catch (error) {
        console.error(error);
      }
    },
    cancel() {
      this.$emit("closeMe");
    },
  },
  created() {
    this.item = this.fields.buyers;
  },
};
</script>
<style lang="scss" scoped>
.slideForm {
  width: 100%;
  @include flexed(column, space-between, center);
  .form-title {
    font-weight: 700;
    font-size: 1.25rem;
    width: 100%;
    color: $light-text;
    background-color: $primary;
    padding: 10px 3px;
    border-radius: $radius $radius 0 0;
    margin-top: -5px;
  }
  .hint {
    text-align: right;
    margin: 5px 0;
    padding: 10px 0;
    border-bottom: 1px solid $line-lighter;
    margin-bottom: 10px;
    width: 100%;
  }
  .inputGroup {
    display: grid;
    grid-template-columns: 20% auto;
    text-align: right;
    align-items: center;
    margin: 0 0 10px;
    width: 100%;
    label {
      color: $dark-text;
      font-size: 1rem;
      font-weight: bold;
    }
  }
  .buttons-bar {
    width: 100%;
    border-top: 1px solid $line-lighter;
    margin-top: 10px;
    padding-top: 10px;
    @include flexed(row, flex-end, center);

    > button {
      margin: 0 10px 0 0;
    }
  }
}
</style>
