<template>
  <div class="login">
    <div class="logo"><img src="../../assets/logo.png" /></div>
    <login-form @onSubmit="login" />
  </div>
</template>

<script>
import { mapActions } from "vuex";
import LoginForm from "../../components/auth/LoginForm.vue";
export default {
  components: { LoginForm },
  name: "Login",
  methods: {
    ...mapActions(["act_login"]),
    async login(e) {
      const result = await this.act_login(e);
      if (!result.result) {
        if (result.code === 401) {
          return this.$toast.error(this.lang.loginError);
        }
        console.log(result);
        return this.$toast.error(this.lang.failedProcess);
      }
      this.$router.push({ name: "Home" });
    },
  },
};
</script>

<style lang="scss" scoped>
.login {
  height: 80vh;
  @include flexed(column, center, center);
}
</style>