import instance from "../helpers/instance";
import tokenStore from "../helpers/localStorage/authTokens";

const state = {
  user: {},
  isAuthenticated: false,
};

const getters = {
  get_user: (state) => state.user,
  isAuthenticated: (state) => state.isAuthenticated,
};

const actions = {
  async act_login({ commit }, userdata) {
    try {
      const user = await instance.post("/auth/signin", userdata);
      if (!user) throw "error";

      commit("SET_USER", user.data);
      tokenStore.set(user.data.tokens.access_token);
      return { result: true };
    } catch (error) {
      return { result: null, code: error.response.status, error: error };
    }
  },
  async act_reAuth({ commit }) {
    try {
      const user = await instance.get("/auth");
      if (!user) throw "error";

      commit("SET_USER", user.data);
      tokenStore.set(user.data.tokens.access_token);
      return { result: true };
    } catch (error) {
      tokenStore.removeItem();
      return { result: null, code: error.response.status, error: error };
    }
  },

  async act_logout({ commit }) {
    commit("REMOVE_USER");
    tokenStore.removeItem();
  },
};

const mutations = {
  SET_USER: (state, data) => {
    state.user = data;
    state.isAuthenticated = true;
  },
  REMOVE_USER: (state) => (state.user = {}),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
