import axios from "axios";

const state = {
  buyers: [],
  loading: false,
};

const getters = {
  All_buyers: (state) => state.buyers,
  loading: (state) => state.loading,
};

const actions = {
  async act_get_all_buyers({ commit }) {
    try {
      commit("loading", true);
      const item = await axios.get(
        `${process.env.VUE_APP_BACKEND}/buyers?all=true`
      );
      if (!item) throw "There is no data.";
      commit("set_buyers", item.data);
      // return true;
    } catch (error) {
      console.log(error);
      return false;
    } finally {
      commit("loading", false);
    }
  },
  async act_add_buyer({ dispatch }, newBuyer) {
    try {
      const insertedItem = await axios.post(
        `${process.env.VUE_APP_BACKEND}/buyers`,
        newBuyer
      );
      if (!insertedItem.data) throw "duplicate";
      dispatch("act_get_all_buyers", {
        root: true,
      });
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_delete_buyer({ commit }, id) {
    try {
      const item = await axios.delete(
        `${process.env.VUE_APP_BACKEND}/buyers/${id}`
      );
      if (!item) throw "There is no data.";
      commit("delete_buyer", id);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },
};

const mutations = {
  set_buyers: (state, data) => (state.buyers = data),
  delete_buyer: (state, id) =>
    (state.buyers = state.buyers.filter((buyer) => buyer.id != id)),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
