import axios from "axios";
import { createHelpers } from "vuex-map-fields";

// Because by default, getters and mutations in Vuex
// modules, are globally accessible and not namespaced,
// you most likely want to rename the getter and mutation
// helpers because otherwise you can't reuse them in multiple,
// non namespaced modules.
const { getBuyerFields, updateBuyerFields } = createHelpers({
  getterType: "getBuyerFields",
  mutationType: "updateBuyerFields",
});

const state = {
  buyer: {},
};

const getters = {
  get_buyer: (state) => state.buyer,
  getBuyerFields,
};

const actions = {
  async act_get_buyer({ commit }, id) {
    try {
      const buyer = await axios.get(
        `${process.env.VUE_APP_BACKEND}/buyers?id=${id}`
      );
      if (!buyer) throw "There is no data.";
      commit("set_buyer", buyer.data);
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  },

  async act_get_buyer_by_sign({ commit }, sign_no) {
    try {
      const buyer = await axios.get(
        `${process.env.VUE_APP_BACKEND}/buyers?sign_no=${sign_no}`
      );
      if (!buyer) throw "There is no data.";
      console.log(buyer.data);
      commit("set_buyer", buyer.data);
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  },

  async act_update_buyer({ commit, dispatch }) {
    try {
      const values = state.buyer;
      const updated = await axios.patch(
        `${process.env.VUE_APP_BACKEND}/buyers/${values.id}`,
        values
      );
      if (updated.status != (200 || 201)) {
        throw "خطا في ادخال البيانات";
      }
      await dispatch("act_get_all_buyers", {}, { root: true });
      commit("clear_buyer_field");
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  },

  act_clear_buyer_fields({ commit }) {
    commit("clear_buyer_field");
  },
};

const mutations = {
  set_buyer: (state, data) => (state.buyer = data),
  clear_buyer_field: (state) => (state.buyer = {}),
  updateBuyerFields,
};

export default {
  state,
  getters,
  actions,
  mutations,
};
