export default function LStore(name) {
  this.name = name;
}

LStore.prototype.set = function (value) {
  localStorage.setItem(this.name, value);
};

LStore.prototype.get = function () {
  return localStorage.getItem(this.name);
};

LStore.prototype.removeItem = function () {
  return localStorage.removeItem(this.name);
};

LStore.prototype.set_json = function () {
  return JSON.stringify(localStorage.getItem(this.name));
};

LStore.prototype.get_json = function () {
  return JSON.parse(localStorage.getItem(this.name));
};
