<template>
  <div class="table-container">
    <Popup v-show="addItemsShow">
      <template v-slot:body>
        <AddItemForm @closeMe="() => (addItemsShow = false)" />
      </template>
    </Popup>
    <Popup v-if="showEditItem">
      <template v-slot:body>
        <EditItemForm :id="editId" @closeMe="() => (showEditItem = false)" />
      </template>
    </Popup>
    <Pad>
      <div class="titleItems">
        <div class="itemListTitle">
          <h3>{{ words.itemsList }}</h3>
          <small>{{ words.itemsListHint }}</small>
        </div>
        <Button
          :title="lang.addItem"
          color="green"
          icon="add"
          shape="circle"
          @onClick="() => (addItemsShow = true)"
        />
      </div>
      <TableList :isResults="getAllItems.length > 0">
        <template v-slot:head>
          <tr>
            <th v-for="column in columns" :key="column">{{ column }}</th>
            <th></th>
          </tr>
        </template>
        <template v-slot:body>
          <tr v-for="(item, key) of getAllItems" :key="item.id">
            <td>{{ key + 1 }}</td>
            <td>{{ item.item_no }}</td>
            <td>{{ item.land_area }}</td>
            <td>{{ item.zone_name }}</td>
            <td>{{ item.doc_no }}</td>
            <td>
              <ListIcons
                :itemId="item.id"
                :trashIcon="true"
                :editIcon="true"
                :addToListIcon="false"
                @trash="trash"
                @edit="editItem"
              />
            </td>
          </tr>
        </template>
      </TableList>
    </Pad>
  </div>
</template>
        

<script>
import ListIcons from "../../../components/icons/ListIcons.vue";
import { mapActions, mapGetters } from "vuex";
import TableList from "../../../components/TableList.vue";
import Pad from "../../../components/layouts/Pad.vue";
import Popup from "../../../components/Popup.vue";
import AddItemForm from "../../../components/forms/items/AddItemForm.vue";
import Button from "../../../components/buttons/Button.vue";
import EditItemForm from "../../../components/forms/items/EditItemForm.vue";

export default {
  components: {
    ListIcons,
    TableList,
    Pad,
    Popup,
    AddItemForm,
    Button,
    EditItemForm,
  },
  name: "Items",
  data() {
    return {
      words: this.lang,
      checked: false,
      enableEdit: false,
      addItemsShow: false,
      showEditItem: false,
      editId: "",
      columns: [
        this.lang.serial,
        this.lang.item_no,
        this.lang.land_area,
        this.lang.zone_name,
        this.lang.doc_no,
      ],
    };
  },
  props: {
    title: String,
    hint: String,
    items: Array,
  },
  computed: {
    ...mapGetters(["getAllItems"]),
  },
  methods: {
    ...mapActions([
      "act_removeOne",
      "act_add_item_to_group",
      "act_remove_item_from_items",
      "act_requestAllItems",
      "act_get_item",
    ]),
    async editItem(id) {
      const res = await this.act_get_item(id);
      if (res) {
        this.showEditItem = true;
      }
    },
    async trash(id) {
      if (!confirm(this.lang.confirm)) return;
      try {
        const res = await this.act_removeOne(id);
        if (!res) return this.$toast.error(this.lang.failedProcess);
        return this.$toast.success(this.lang.successProcess);
      } catch (error) {
        console.error(error);
        return this.$toast.error(this.lang.failedProcess);
      }
    },
  },
  mounted() {},
  async created() {
    await this.act_requestAllItems();
  },
};
</script>

<style lang="scss" scoped>
.table-container {
  @include flexed(row, space-between, flex-start);
  .titleItems {
    @include flexed(row, space-between, center);
    .itemListTitle {
      width: 50%;
      text-align: right;
      margin: 0 0 5px 0;
      h3 {
        margin-bottom: 1px;
      }
    }
  }
}
</style>
