<template>
  <div class="round-list-view">
    <Popup v-if="showCreateContractWin">
      <template v-slot:body
        ><CreateContractWin
          @closeMe="() => (showCreateContractWin = !showCreateContractWin)"
      /></template>
    </Popup>
    <div class="table-container">
      <ListTitle :title="lang.roundsList" :hint="lang.roundsListHint" />
      <TableList :isResults="all_rounds.length > 0">
        <template v-if="all_rounds.length > 0" v-slot:head>
          <tr>
            <th>{{ words.serial }}</th>
            <th>{{ words.itemsNumbers }}</th>
            <th>{{ words.start_value }}</th>
            <th>{{ words.step_value }}</th>
            <th>{{ words.totalAreas }}</th>
            <th></th>
          </tr>
        </template>
        <template v-slot:body>
          <tr
            v-for="(item, key) in all_rounds"
            :key="item.id"
            @dblclick="showInScreen(item.id)"
          >
            <td>{{ key + 1 }}</td>
            <td>{{ item.items_no }}</td>
            <td>{{ item.start_value }}</td>
            <td>{{ item.step_value }}</td>
            <td>{{ item.land_areas }}</td>
            <td>
              <ListIcons
                :itemId="item.id"
                :trashIcon="true"
                :screenEmpty="item.status === 0"
                :screenOn="item.status === 1"
                :screenDone="item.status === 2"
                @trash="trash"
                @screenOn="showOff"
                @screenEmpty="showInScreen"
                @screenDone="openCreateContractWin"
              />
            </td>
          </tr>
        </template>
      </TableList>
    </div>
    <div class="table-footer">
      <small>{{ `${words.itemsCount} ${all_rounds.length}` }}</small>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import ListIcons from "../icons/ListIcons.vue";
import ListTitle from "../ListTitle.vue";
import Popup from "../Popup.vue";
import TableList from "../TableList.vue";
import CreateContractWin from "../windows/contract/CreateContractWin.vue";

export default {
  components: { ListIcons, TableList, ListTitle, Popup, CreateContractWin },
  name: "RoundsList",
  data() {
    return {
      words: this.lang,
      showingOn: false,
      showCreateContractWin: false,
    };
  },
  props: {
    title: String,
    hint: String,
  },
  computed: {
    ...mapGetters(["all_rounds", "all_settings"]),
  },
  methods: {
    ...mapActions([
      "act_remove_round",
      "act_get_all_rounds",
      "act_update_round",
      "act_get_round",
    ]),
    async showInScreen(roundId) {
      try {
        const value = { id: roundId, values: { status: 1 } };
        this.act_update_round(value);
        this.$toast.success(this.lang.screenOnSuccess);
        this.showingOn = true;
      } catch (error) {
        this.$toast.error(this.lang.failedProcess);
        console.error(error);
      }
    },
    showOff(roundId) {
      if (confirm(this.lang.screenOfConfirm))
        try {
          const value = { id: roundId, values: { status: 0 } };
          this.act_update_round(value);
          this.showingOn = false;
        } catch (error) {
          this.$toast.error(this.lang.failedProcess);
          console.error(error);
        }
    },
    async openCreateContractWin(roundId) {
      try {
        await this.act_get_round(roundId);
        this.showCreateContractWin = true;
      } catch (error) {
        console.error(error);
        this.$toast.error(this.lang.failedProcess);
      }
    },
    trash(id) {
      console.log(id);
      this.act_remove_round(id);
    },
  },

  created() {
    this.act_get_all_rounds();
  },
};
</script>

<style lang="scss" scoped>
.round-list-view {
  width: auto;
  .table-container {
    @include flexed(column, flex-start, flex-start);
  }
  .table-footer {
    text-align: right;

    border-top: 1px solid $line-lighter;
    padding: 5px 5px;
    margin: 20px 0;
  }
}
</style>
