import { getField, updateField } from "vuex-map-fields";
import ErrorsHandler from "../helpers/errors";
import instance from "../helpers/instance";

import storage from "../helpers/localStorage/main";
const state = {
  settings: {},
};

const getters = {
  all_settings: (state) => state.settings,
  getField,
};

const actions = {
  async act_get_settings({ commit }) {
    try {
      const settings = await instance.get("/settings");
      if (!settings) throw "There is no data.";
      storage.settings.set_json(settings);
      commit("set_settings", settings.data);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },
  async act_update_settings() {
    try {
      const newSettings = {
        estate_tax: state.settings.estate_tax,
        commission: state.settings.commission,
        vat_tax: state.settings.vat_tax,
        show_vat_tax: state.settings.show_vat_tax,
        plan_no: state.settings.plan_no,
      };
      const settings = await instance.patch("/settings", newSettings);

      if (!settings) throw new ErrorsHandler("Failed process!", false);
      return { state: true };
    } catch (error) {
      console.log(error);
      return new ErrorsHandler(error.message, false, error);
    }
  },
};

const mutations = {
  set_settings: (state, data) => (state.settings = data),
  updateField,
};

export default {
  state,
  getters,
  actions,
  mutations,
};
