<template>
  <div class="payments-container">
    <Popup v-show="addPaymentShow">
      <template v-slot:body>
        <AddPaymentForm
          :buyerId="buyerId"
          @closeMe="() => (addPaymentShow = false)"
        />
      </template>
    </Popup>
    <div class="titleItems">
      <div class="itemListTitle">
        <h3>{{ lang.payments_list }}</h3>
        <small>{{ lang.payments_list_Hint }}</small>
      </div>
      <Button
        :title="lang.addPayment"
        color="green"
        icon="add"
        shape="circle"
        @onClick="() => (addPaymentShow = true)"
      />
    </div>
    <TableList :isResults="buyer_payments != null">
      <template v-slot:head>
        <tr>
          <th v-for="column in columns" :key="column">{{ column }}</th>
          <th></th>
        </tr>
      </template>
      <template v-slot:body>
        <tr
          v-for="(payment, key) of buyer_payments"
          :key="payment.id"
          :tabindex="key"
        >
          <td>{{ key + 1 }}</td>
          <td>{{ payment.check_no }}</td>
          <td>{{ payment.amount }}</td>
          <td>
            <ListIcons
              :itemId="payment.id"
              :trashIcon="true"
              :editIcon="false"
              :addToListIcon="false"
              @trash="trash"
            />
          </td>
        </tr>
      </template>
    </TableList>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Button from "../../../components/buttons/Button.vue";
import AddPaymentForm from "../../../components/forms/buyers/AddPaymentForm.vue";
import ListIcons from "../../../components/icons/ListIcons.vue";
import Popup from "../../../components/Popup.vue";
import TableList from "../../../components/TableList.vue";

export default {
  Name: "Payments",
  components: { TableList, Button, ListIcons, AddPaymentForm, Popup },
  props: ["buyerId"],
  data() {
    return {
      columns: [this.lang.serial, this.lang.check_no, this.lang.amount],
      addPaymentShow: false,
      selectedBuyer: null,
    };
  },
  computed: {
    ...mapGetters(["buyer_payments"]),
  },
  methods: {
    ...mapActions(["act_delete_payment", "act_get_buyer_payments"]),
    async trash(payment_id) {
      if (!confirm(this.lang.confirm)) return;
      try {
        const res = await this.act_delete_payment(payment_id);
        if (!res) return this.$toast.error(this.lang.failedProcess);
        return this.$toast.success(this.lang.successProcess);
      } catch (error) {
        console.error(error);
        return this.$toast.error(this.lang.failedProcess);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.payments-container {
  .titleItems {
    @include flexed(row, space-between, center);
    .itemListTitle {
      width: 100%;
      text-align: right;
      margin: 0 0 5px 0;
      h3 {
        margin-bottom: 1px;
      }
    }
  }
}
</style>