<template>
  <form @submit.prevent="onSubmit">
    <div class="search-from-container">
      <SearchForm :placeHolder="lang.findBySingNo" @searchValue="findBuyer" />
    </div>
    <div class="search-buyer">
      <div class="infoCard">
        <div class="form-group">
          <label>{{ lang.fullname }}</label>
          <InputField type="text" :value="buyer.fullname" disabled />
        </div>
        <div class="form-group">
          <label>{{ lang.national_id }}</label>
          <InputField type="text" :value="buyer.national_id" disabled />
        </div>
        <div class="form-group">
          <label>{{ lang.phone }}</label>
          <InputField type="text" :value="buyer.phone" disabled />
        </div>

        <ul class="payments-box">
          <li class="payment-row">
            <ul>
              <li class="payment-cell">#</li>
              <li class="payment-cell">{{ lang.check_no }}</li>
              <li class="payment-cell">{{ lang.amount }}</li>
            </ul>
          </li>
          <li
            class="payment-row"
            v-for="(payment, key) in buyer.payments"
            :key="key"
          >
            <ul>
              <li class="payment-cell">
                <input
                  type="checkbox"
                  :id="payment.id"
                  :value="payment.id"
                  v-model="checkedPayments"
                />
              </li>
              <li class="payment-cell">{{ payment.check_no }}</li>
              <li class="payment-cell">{{ payment.amount }}</li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
    <div class="btn-search_buyer">
      <Button
        color="green"
        icon="save"
        shape="normal"
        :text="lang.create"
        type="submit"
      />
      <Button
        color="red"
        icon="cancel"
        shape="circle"
        :text="lang.cancel"
        @onClick="$emit('closeMe')"
      />
    </div>
  </form>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Button from "../../buttons/Button.vue";
import SearchForm from "../../forms/searchForm.vue";
import InputField from "../../inputs/InputField.vue";
export default {
  data() {
    return {
      checkedPayments: [],
      buyer: {
        id: null,
        fullname: null,
        national_id: null,
        phone: null,
      },
    };
  },
  components: { InputField, SearchForm, Button },
  computed: {
    ...mapGetters(["get_buyer"]),
  },
  methods: {
    ...mapActions(["act_get_buyer", "act_clear_buyer_fields"]),
    onSubmit() {
      if (this.checkedPayments.length === 0) {
        return this.$toast.error(this.lang.choiceOnPayment);
      }
      return this.$emit("onSubmit", {
        buyerId: this.buyer.id,
        payments: JSON.parse(JSON.stringify(this.checkedPayments)),
      });
    },
    async findBuyer(signNo) {
      try {
        const result = await this.act_get_buyer(signNo);
        if (!result) throw " not complate";
        this.buyer = this.get_buyer;
      } catch (error) {
        this.$toast.error(this.lang.failedProcess);
        console.error(error);
      }
    },
  },
};
</script>

   
<style lang="scss" scoped>
.search-from-container {
  @include flexed(column, flex-start, flex-start);
  max-width: 290px;
}
.search-buyer {
  width: 100%;
  @include flexed(column, space-between, center);

  .infoCard {
    width: auto;
    margin: 10px 0;
    border-radius: $radius;
    border: 1px solid $line-lighter;
    padding: 10px;
    @include flexed(row, space-between, center);
    flex-wrap: wrap;
    .payments-box {
      display: table;
      width: 100%;
      border: 1px solid $line-lighter;
      .payment-row {
        display: table-caption;
        border-bottom: 1px solid $line-lighter;
        width: 100%;
        &:first-child {
          background-color: $line-lighter;
        }
        .payment-cell {
          display: table-cell;

          padding: 10px 0;
          width: 200px;
          &:first-child {
            width: 50px;
          }
        }
      }
    }
    .form-group {
      @include flexed(row, space-between, center);
      flex-wrap: wrap;

      label {
        margin: 0 20px 0 20px;
      }
      input {
        margin: 5px 0;
        max-width: 200px;
      }
    }
  }
  &:first-child {
    column-span: 2;
  }
}
.btn-search_buyer {
  @include flexed(row, space-between, flex-end);
  width: 100%;
}
</style>