<template>
  <div class="table-container">
    <Popup v-show="addItemsShow">
      <template v-slot:body>
        <AddBuyerForm @closeMe="() => (addItemsShow = false)" />
      </template>
    </Popup>
    <Popup v-if="showEditItem">
      <template v-slot:body>
        <EditBuyerForm :id="editId" @closeMe="() => (showEditItem = false)" />
      </template>
    </Popup>
    <Pad>
      <div class="titleItems">
        <div class="itemListTitle">
          <h3>{{ words.buyersList }}</h3>
          <small>{{ words.buyersListHint }}</small>
        </div>
        <Button
          :title="lang.addItem"
          color="green"
          icon="add"
          shape="circle"
          @onClick="() => (addItemsShow = true)"
        />
      </div>
      <TableList :isResults="All_buyers.length > 0">
        <template v-slot:head>
          <tr>
            <th v-for="column in columns" :key="column">{{ column }}</th>
            <th></th>
          </tr>
        </template>
        <template v-slot:body>
          <tr
            v-for="(buyer, key) of All_buyers"
            :class="{ highlight: buyer.id === selectedID }"
            :key="buyer.id"
            :tabindex="key"
            autofocus
            @focus="showPayments(buyer.id)"
          >
            <td>{{ key + 1 }}</td>
            <td>{{ buyer.sign_no }}</td>
            <td>{{ buyer.fullname }}</td>
            <td>{{ buyer.national_id }}</td>
            <td>{{ buyer.nationalty }}</td>
            <td>{{ buyer.phone }}</td>
            <td>{{ buyer.email }}</td>
            <td>
              <ListIcons
                :itemId="buyer.id"
                :trashIcon="true"
                :editIcon="true"
                :addToListIcon="false"
                @trash="trash"
                @edit="editItem"
              />
            </td>
          </tr>
        </template>
      </TableList>
    </Pad>
    <div class="payments-side">
      <Pad>
        <Payments v-if="selectedID" :buyerId="selectedID" />
        <div v-else class="empty">{{ lang.noResults }}</div>
      </Pad>
    </div>
  </div>
</template>
        

<script>
import ListIcons from "../../../components/icons/ListIcons.vue";
import { mapActions, mapGetters } from "vuex";
import TableList from "../../../components/TableList.vue";
import Pad from "../../../components/layouts/Pad.vue";
import Popup from "../../../components/Popup.vue";
import AddBuyerForm from "../../../components/forms/buyers/AddbuyerForm.vue";
import Button from "../../../components/buttons/Button.vue";
import EditBuyerForm from "../../../components/forms/buyers/EditBuyerForm.vue";
import Payments from "./Payments.vue";

export default {
  components: {
    ListIcons,
    TableList,
    Pad,
    Popup,
    AddBuyerForm,
    Button,
    EditBuyerForm,
    Payments,
  },
  name: "Buyers",
  data() {
    return {
      paymentsList: "",
      //** other */
      words: this.lang,
      checked: false,
      enableEdit: false,
      addItemsShow: false,
      showEditItem: false,
      selectedID: null,
      editId: "",
      selectedUser: "",
      columns: [
        this.lang.serial,
        this.lang.sign_no,
        this.lang.fullname,
        this.lang.national_id,
        this.lang.nationalty,
        this.lang.phone,
        this.lang.email,
      ],
    };
  },
  props: {
    title: String,
    hint: String,
    items: Array,
  },
  computed: {
    ...mapGetters(["All_buyers"]),
  },
  methods: {
    ...mapActions([
      "act_get_all_buyers",
      "act_get_buyer",
      "act_delete_buyer",
      "act_get_buyer_payments",
    ]),

    async showPayments(buyerId) {
      //  const buyer = this.All_buyers.filter((buyer) => buyer.id === buyerId)[0];
      this.selectedID = buyerId;
      await this.act_get_buyer_payments(buyerId);
    },
    async editItem(id) {
      const res = await this.act_get_buyer(id);
      if (res) {
        this.showEditItem = true;
      }
    },
    async trash(id) {
      if (!confirm(this.lang.confirm)) return;
      try {
        const res = await this.act_delete_buyer(id);
        if (!res) return this.$toast.error(this.lang.failedProcess);
        return this.$toast.success(this.lang.successProcess);
      } catch (error) {
        console.error(error);
        return this.$toast.error(this.lang.failedProcess);
      }
    },
  },

  async created() {
    await this.act_get_all_buyers();
  },
};
</script>

<style lang="scss" scoped>
.highlight {
  background-color: $green;
  font-weight: 700;
}
.table-container {
  @include flexed(row, space-between, flex-start);
  column-gap: 30px;

  .payments-side {
    max-width: 30%;
    min-width: 600px;
    .empty {
      height: 100px;
      width: 100px;
    }
  }

  .titleItems {
    @include flexed(row, space-between, center);
    .itemListTitle {
      width: 50%;
      text-align: right;
      margin: 0 0 5px 0;
      h3 {
        margin-bottom: 1px;
      }
    }
  }
}
</style>
