<template>
  <form @submit.prevent="saveChanges" class="slideForm">
    <div class="form-title">
      {{ lang.editBuyer }}
    </div>
    <div class="hint">
      {{ lang.addItemHint }}
    </div>
    <div
      class="inputGroup"
      v-for="(field, key) in Object.keys(fields.buyers)"
      :key="key"
    >
      <label>{{ lang[field] }}</label>
      <InputField
        type="text"
        v-model="buyer[field]"
        :value="buyer[field]"
        :placeholder="lang[field]"
      />
    </div>
    <div class="buttons-bar">
      <Button type="submit" :text="lang.save" icon="save" />
      <Button color="red" :text="lang.cancel" icon="cancel" @onClick="cancel" />
    </div>
  </form>
</template>

<script>
import { createHelpers } from "vuex-map-fields";
import { mapActions } from "vuex";
import Button from "../../buttons/Button.vue";
import InputField from "../../inputs/InputField.vue";

const { mapFields } = createHelpers({
  getterType: "getBuyerFields",
  mutationType: "updateBuyerFields",
});

export default {
  name: "EditBuyerForm",
  components: { InputField, Button },

  data() {
    return {
      lang: this.lang,
    };
  },
  computed: {
    ...mapFields(["buyer"]),
  },
  methods: {
    ...mapActions(["act_update_buyer"]),
    cancel() {
      this.$emit("closeMe");
    },
    async saveChanges() {
      try {
        const res = await this.act_update_buyer();
        if (!res) return this.$toast.error(this.lang.failedProcess);
        this.$emit("closeMe");
        return this.$toast.success(this.lang.successProcess);
      } catch (error) {
        console.log(error);
        return this.$toast.error(this.lang.failedProcess);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.slideForm {
  width: 100%;
  @include flexed(column, space-between, center);
  .form-title {
    font-weight: 700;
    font-size: 1.25rem;
    width: 100%;
    color: $light-text;
    background-color: $primary;
    padding: 10px 3px;
    border-radius: $radius $radius 0 0;
    margin-top: -5px;
  }
  .hint {
    text-align: right;
    margin: 5px 0;
    padding: 10px 0;
    border-top: 1px solid $line-lighter;
    width: 100%;
  }
  .inputGroup {
    display: grid;
    grid-template-columns: 20% auto;
    text-align: right;
    align-items: center;
    margin: 0 0 10px;
    width: 100%;
    label {
      color: $dark-text;
      font-size: 1rem;
      font-weight: bold;
    }
  }
  .buttons-bar {
    width: 100%;
    border-top: 1px solid $line-lighter;
    margin-top: 10px;
    padding-top: 10px;
    @include flexed(row, flex-end, center);

    > button {
      margin: 0 10px 0 0;
    }
  }
}
</style>
