const tools = {};
/**this make an object to be line  */
tools.objectToline = (columns) => {
  const columnsName = Object.keys(columns);
  return columnsName.map((key) => key + " " + columns[key]).join(", ");
};

tools.groupRoundValues = (roundsArray) => {
  const all_rounds = [];

  roundsArray.forEach((round) => {
    const numbers = [];
    const land_area = [];
    const relate = round.relateRoundsItem;
    relate.forEach((item) => {
      const items = item.item.item_no;
      const lands = item.item.land_area;
      numbers.push(items);
      land_area.push(lands);
    });
    const total_lands = land_area.reduce((total, next) => total + next);

    const res = {
      id: round.id,
      items_no: numbers.join(" / "),
      items_no_array: numbers,
      items_count: numbers.length,
      land_areas: total_lands,
      start_value: parseFloat(round.start_value),
      step_value: parseFloat(round.step_value),
      steps_count: round.steps_count,
      status: round.status,
      metter_price:
        parseFloat(round.start_value) +
        parseFloat(round.step_value) * parseInt(round.steps_count),
      is_finish: round.is_finish,
    };
    all_rounds.push(res);
  });

  return all_rounds;
};

tools.groupContractValues = (contractArray) => {
  const all_contracts = [];

  contractArray.forEach((contract) => {
    const numbers = [];
    const land_area = [];
    const round = contract.round;
    const relateRoundsItem = round.relateRoundsItem;
    relateRoundsItem.forEach((item) => {
      const items = item.item.item_no;
      const lands = item.item.land_area;
      numbers.push(items);
      land_area.push(lands);
    });

    const buyer = contract.buyer;
    const payments = contract.relateContractsPayments;
    const checks = [];
    const amount = [];

    payments.forEach((pay_relate) => {
      const paymentField = pay_relate.payment;
      checks.push({
        check_no: paymentField.check_no,
        amount: paymentField.amount,
      });
      amount.push(paymentField.amount);
    });

    const total_lands = land_area.reduce((total, next) => total + next);
    const firstPayment = amount.reduce(
      (total, next) => parseFloat(total) + parseFloat(next)
    );

    const res = {
      id: contract.id,
      metter_price: parseFloat(contract.metter_price),
      items_no: numbers.join(" / "),
      items_count: numbers.length,
      land_areas: parseFloat(total_lands),
      first_payment: parseFloat(firstPayment),
      total_value: parseFloat(total_lands) * parseFloat(contract.metter_price),
      buyer: buyer,
      checks: checks,
      is_finish: contract.is_finish,
    };
    all_contracts.push(res);
  });

  return all_contracts;
};

export default tools;
