<template>
  <div class="main-page">
    <Popup v-show="showSettings">
      <template v-slot:body>
        <Settings @closeMe="() => (showSettings = false)"
      /></template>
    </Popup>
    <TitleBar
      :title="title"
      icon="settings"
      :iconLabel="lang.settings"
      :showIcon="true"
      @onClick="showSettingsWindow"
    />
    <div class="page-container">
      <Pad>
        <AllItemsListWin />
      </Pad>

      <Pad>
        <GroupingItemsList />
      </Pad>
      <div class="l-side">
        <Pad>
          <RoundsList />
        </Pad>

        <Pad>
          <ContractsList />
        </Pad>
        <Pad>
          <div class="buttom-btns">
            <Button
              color="green"
              :text="lang.showScreen"
              @onClick="openShowScreen"
            />
          </div>
        </Pad>
      </div>
    </div>
  </div>
</template>

<script>
import Pad from "../../components/layouts/Pad.vue";
import TitleBar from "../../components/TitleBar.vue";
import AllItemsListWin from "../../components/windows/All_ItemsListWin.vue";

import GroupingItemsList from "../../components/lists/GroupingItemsList.vue";
import RoundsList from "../../components/lists/RoundsList.vue";
import { mapActions } from "vuex";
import Button from "../../components/buttons/Button.vue";
import ContractsList from "../../components/lists/ContractsList.vue";
import Popup from "../../components/Popup.vue";
import Settings from "../../components/forms/Settings.vue";

export default {
  name: "MainPage",
  components: {
    Pad,
    TitleBar,
    AllItemsListWin,
    GroupingItemsList,
    RoundsList,
    Button,
    ContractsList,
    Popup,
    Settings,
  },
  data() {
    return {
      title: this.lang.cpanel,
      lang: this.lang,
      showSettings: false,
    };
  },
  methods: {
    ...mapActions(["act_get_settings"]),
    showSettingsWindow() {
      this.showSettings = true;
    },
    openShowScreen() {
      this.$router.push({ name: "ShowScreen" });
    },
  },
  created() {
    this.act_get_settings();
  },
};
</script>

