<template>
  <div class="round-list-view">
    <div class="table-container">
      <ListTitle :title="lang.contractsList" :hint="lang.contractsListHint" />
      <TableList :isResults="get_contacts.length > 0">
        <template v-slot:head>
          <tr>
            <th>{{ words.serial }}</th>
            <th>{{ words.itemsNumbers }}</th>
            <th>{{ words.itemsCount }}</th>
            <th>{{ words.totalArea }}</th>
            <th>{{ words.metterPrice }}</th>
            <th></th>
          </tr>
        </template>
        <template v-slot:body>
          <tr v-for="(item, key) in get_contacts" :key="item.id">
            <td>{{ key + 1 }}</td>
            <td>{{ item.items_no }}</td>
            <td>{{ item.items_count }}</td>
            <td>{{ item.land_areas }}</td>
            <td>{{ item.metter_price }}</td>
            <td>
              <ListIcons
                :itemId="item.id"
                :trashIcon="true"
                :forwordIcon="true"
                @trash="trash"
              />
            </td>
          </tr>
        </template>
      </TableList>
    </div>
    <div class="table-footer">
      <small>{{ `${words.itemsCount} ${get_contacts.length}` }}</small>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import ListIcons from "../icons/ListIcons.vue";
import ListTitle from "../ListTitle.vue";
import TableList from "../TableList.vue";

export default {
  components: { ListIcons, TableList, ListTitle },
  name: "RoundsList",
  data() {
    return {
      words: this.lang,
      showingOn: false,
      showCreateContractWin: false,
    };
  },
  props: {
    title: String,
    hint: String,
  },
  computed: {
    ...mapGetters(["get_contacts"]),
  },
  methods: {
    ...mapActions(["act_get_all_Contracts", "act_remove_contract"]),
    async trash(contractId) {
      if (!confirm(this.lang.confirm)) return;
      try {
        const result = await this.act_remove_contract(contractId);
        if (!result) throw "error with delete contract";
        this.$toast.success(this.lang.successProcess);
      } catch (error) {
        this.$toast.error(this.lang.failedProcess);
        console.error(error);
      }
    },
  },
  async created() {
    await this.act_get_all_Contracts();
  },
};
</script>

<style lang="scss" scoped>
.round-list-view {
  width: auto;
  .table-container {
    @include flexed(column, flex-start, flex-start);
  }
  .table-footer {
    text-align: right;

    border-top: 1px solid $line-lighter;
    padding: 5px 5px;
    margin: 20px 0;
  }
}
</style>
