<template>
  <div class="show-page">
    <Header />
    <div class="bigMessage" :class="{ moveIT: showBigMessage }">
      {{ round_show_data.metter_price || "" }}
    </div>
    <Pad>
      <div class="show-body">
        <ShowFields :values="round_show_data" />
        <div class="plan-image">
          <Image :itemsNumbers="round_show_data.items_no_array" />
        </div>
      </div>
    </Pad>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import ShowFields from "../../components/forms/show/ShowFields.vue";
import Header from "../../components/Header.vue";
import Pad from "../../components/layouts/Pad.vue";
import Image from "../../components/Image.vue";

export default {
  components: { Header, Pad, ShowFields, Image },
  name: "ShowScreen",
  data() {
    return {
      showBigMessage: false,
    };
  },
  methods: {
    ...mapActions([
      "act_up_step",
      "act_down_step",
      "act_get_show_round",
      "act_update_round",
      "act_clear_round",
    ]),
    toggelShow() {
      this.showBigMessage = !this.showBigMessage;
    },

    async keyHandler(e) {
      if (e.key === "ArrowUp" || e.code === "Space") {
        this.act_up_step();

        this.showBigMessage = true;
        setTimeout(() => (this.showBigMessage = false), 1000);
        console.log("upp");
      }
      if (e.key === "ArrowDown") {
        this.act_down_step();
      }
      if (e.key === "Enter") {
        try {
          const data = JSON.parse(JSON.stringify(this.round_show_data));
          const values = {
            id: data.id,
            values: { status: 2, steps_count: data.steps_count },
          };
          const results = await this.act_update_round(values);
          if (!results) throw "error";
          this.act_clear_round();
        } catch (error) {
          console.error(error);
        }
      }
      if (e.key === "c") {
        this.act_clear_round();
      }

      if (e.key === "l") {
        try {
          const results = await this.act_get_show_round();
          if (!results) throw "error";
        } catch (error) {
          console.error(error);
        }
      }
    },
  },
  computed: {
    ...mapGetters(["round_show_data"]),
  },
  async created() {
    this.act_clear_round();
    window.addEventListener("keyup", this.keyHandler);
  },
};
</script>

<style lang="scss" scoped>
.show-page {
  width: 100%;
  @include flexed(column, center, center);

  .show-body {
    width: 92vw;
    display: grid;
    grid-template-columns: 700px auto;
    .plan-image {
      width: 100%;
    }
  }
}
.bigMessage {
  position: absolute;
  top: auto;
  bottom: auto;
  left: auto;
  right: auto;
  font-size: 0px;
  z-index: 1;
  color: red;
  font-weight: bold;
}
.moveIT {
  animation: jumpText 1.5s ease-in-out;
  animation-iteration-count: 1;
}
@keyframes jumpText {
  0% {
    font-size: 0px;
    color: red;
  }
  33% {
    font-size: 400px;
    color: $primary;
  }
  66% {
    font-size: 250px;
    color: red;
  }
  100% {
    font-size: 0px;
    color: red;
  }
}
</style>
