<template>
  <form
    @submit.prevent="$emit('values', { step_value, start_value })"
    class="slideForm"
  >
    <div class="hint">
      {{ lang.addItemHint }}
    </div>
    <div class="inputGroup">
      <label>{{ lang.step_value }}</label>
      <InputField
        type="number"
        v-model="step_value"
        :value="step_value"
        :placeholder="lang.step_value"
        required
        @invalid="invalid"
      />
    </div>
    <div class="inputGroup">
      <label>{{ lang.start_value }}</label>
      <InputField
        type="number"
        v-model="start_value"
        :value="start_value"
        :placeholder="lang.start_value"
        required
      />
    </div>
    <div class="buttons-bar">
      <Button type="submit" :text="lang.save" icon="save" />
      <Button color="red" :text="lang.cancel" icon="cancel" @onClick="cancel" />
    </div>
  </form>
</template>

<script>
import Button from "../buttons/Button.vue";
import InputField from "../inputs/InputField.vue";

export default {
  name: "GreateBid",
  components: { InputField, Button },
  data() {
    return { step_value: "", start_value: "", lang: this.lang };
  },
  methods: {
    invalid() {
      this.$toast.error(this.lang.fillRequired);
    },
    cancel() {
      this.step_value = "";
      this.start_value = "";
      this.$emit("cancel");
    },
  },
};
</script>
<style lang="scss" scoped>
.slideForm {
  width: 100%;
  @include flexed(column, space-between, center);
  .hint {
    text-align: right;
    margin: 5px 0;
    padding: 10px 0;
    border-top: 1px solid $line-lighter;
    width: 100%;
  }
  .inputGroup {
    @include flexed(row, space-between, center);
    margin: 0 0 10px;
    width: 100%;
    label {
      color: $dark-text;
      font-size: 1rem;
      font-weight: bold;
    }
  }
  .buttons-bar {
    width: 100%;
    @include flexed(row, flex-end, center);

    > button {
      margin: 0 10px 0 0;
    }
  }
}
</style>
