<template>
  <Pad>
    <form @submit.prevent="saveItem" class="slideForm">
      <div class="hint">
        {{ lang.addItemHint }}
      </div>
      <div class="inputGroup">
        <label>{{ lang.itemNumber }}</label>
        <InputField
          type="number"
          :placeholder="lang.itemNumber"
          v-model="item.i_number"
          :value="item.i_number"
        />
      </div>
      <div class="inputGroup">
        <label>{{ lang.landArea }}</label>
        <InputField
          type="number"
          :placeholder="lang.landArea"
          v-model="item.i_land_area"
          :value="item.i_land_area"
        />
      </div>

      <div class="buttons-bar">
        <Button type="submit" :text="lang.save" icon="save" />
        <Button
          color="red"
          :text="lang.cancel"
          icon="cancel"
          @onClick="cancel"
        />
      </div>
    </form>
  </Pad>
</template>

<script>
import Button from "../../components/buttons/Button.vue";

import { createHelpers } from "vuex-map-fields";
import { mapActions } from "vuex";
import InputField from "../../components/inputs/InputField.vue";
import Pad from "../../components/layouts/Pad.vue";

// The getter and mutation types we're providing
// here, must be the same as the function names we've
// used in the store.
const { mapFields } = createHelpers({
  getterType: "getItemFields",
  mutationType: "updateItemFields",
});

export default {
  name: "ItemEdit",
  components: { Button, InputField, Pad },
  data() {
    return { lang: this.lang };
  },
  methods: {
    ...mapActions(["act_get_item", "act_update_item"]),
    async saveItem() {
      const saved = await this.act_update_item();
      if (!saved) return this.$toast.error(this.lang.failedProcess);
    },
  },
  computed: {
    ...mapFields(["item"]),
  },
  async created() {
    await this.act_get_item(this.$route.params.id);
  },
};
</script>
<style lang="scss" scoped>
.slideForm {
  width: 100%;
  @include flexed(column, space-between, center);
  .hint {
    text-align: right;
    margin: 5px 0;
    padding: 10px 0;
    border-top: 1px solid $line-lighter;
    width: 100%;
  }

  .inputGroup {
    @include flexed(row, space-between, center);
    margin: 0 0 10px;
    width: 100%;
  }

  .buttons-bar {
    width: 100%;
    @include flexed(row, flex-end, center);

    > button {
      margin: 0 10px 0 0;
    }
  }
}
</style>
