<script>
export default {
  name: "Image",
  props: ["itemsNumbers"],
  methods: {
    myFunctionClick: function () {
      this.$refs.item_53.setAttribute("fill", "red");
    },
  },
  mounted() {
    if (this.itemsNumbers) {
      this.itemsNumbers.forEach((number) => {
        const element = document.getElementById(`item-${number}`);
        if (element) element.setAttribute("class", "flashing");
      });
    }
  },
};
</script>

<style lang="scss" scoped>
svg {
  max-height: 70vh;
  border: 1px solid $line-lighter;
  border-radius: $radius;
  @include flexed(row, center, center);
}
.flashing {
  fill: red;
  animation: flash 1s ease-in-out infinite;
}
@keyframes flash {
  0% {
    fill: red;
  }
  100% {
    fill: white;
  }
}
.st0 {
  fill: #ffffff;
}
</style>