<template>
  <div class="importFileContainer">
    <h3>اختر الملف المطلوب</h3>
    <InputField type="file" @getFiles="getFiles" accept="csv" />
    <small>{{ words.importHint }}</small>
    <Button @onClick="$emit('getFile', files)" :text="words.importIt" />
  </div>
</template>

<script>
import Button from "../buttons/Button.vue";
import InputField from "../inputs/InputField.vue";
export default {
  name: "ImportFile",
  components: { InputField, Button },
  data() {
    return {
      words: this.lang,
      files: "",
    };
  },
  methods: {
    getFiles(e) {
      this.files = e;
    },
  },
};
</script>

<style lang="scss" scoped>
.importFileContainer {
  width: 100%;
  @include flexed(column, center, space-evenly);
}
</style>