import axios from "axios";

const state = {
  payments: {},
};

const getters = {
  buyer_payments: (state) => state.payments,
};

const actions = {
  async act_get_buyer_payments({ commit }, id) {
    try {
      const buyer = await axios.get(
        `${process.env.VUE_APP_BACKEND}/payments?buyerId=${id}`
      );
      if (!buyer) throw "There is no data.";

      commit("SET_PAYMENTS", buyer.data);
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  },

  async act_add_payment({ commit }, { id, payment }) {
    console.log(payment, payment);
    try {
      const insertedItem = await axios.post(
        `${process.env.VUE_APP_BACKEND}/buyers/${id}/payments`,
        payment
      );
      console.log(insertedItem.data.payments);
      if (!insertedItem.data) throw "duplicate";
      commit("ADD_NEW_PAYMENT", insertedItem.data);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_delete_payment({ commit }, id) {
    try {
      const insertedItem = await axios.delete(
        `${process.env.VUE_APP_BACKEND}/payments/${id}`
      );
      if (!insertedItem.data) throw "error with delete order";
      commit("DELETE_PAYMENT", id);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_update_buyer() {},
};

const mutations = {
  SET_PAYMENTS: (state, data) => (state.payments = data),
  ADD_NEW_PAYMENT: (state, data) => (state.payments = data.payments),
  DELETE_PAYMENT: (state, id) =>
    (state.payments = state.payments.filter((payment) => payment.id != id)),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
