<template>
  <button
    class="button"
    :class="color + ' ' + shape"
    :type="type"
    :color="color"
    @click="$emit('onClick')"
    :title="title"
  >
    <span class="spanNormal" v-if="shape === 'normal'"> {{ text }}</span
    ><Icon :name="icon" />
  </button>
</template>

<script>
import Icon from "../icons/Icon.vue";

export default {
  name: "Button",
  components: { Icon },
  props: {
    shape: { default: "normal", type: String },
    type: { default: "button", type: String },
    text: { default: "button", type: String },
    color: { default: "green", type: String },
    title: { type: String },
    icon: String,
  },
};
</script>

<style lang="scss" scoped>
.button {
  border: 0;
  font-size: 1rem;
  color: $btn-text;
  font-family: inherit;
  cursor: pointer;

  &:hover {
    filter: brightness(90%);
  }
  &:focus {
    outline: 0;
  }
  &:active {
    filter: brightness(80%);
  }
}
.normal {
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  padding: 8px 32px;
  height: 40px;
  border-radius: 6px;
  @include flexed(row, space-between, center);

  border: 1px solid $line-lighter;
  .spanNormal {
    float: right;
    margin: 0 10px;
  }
}
.circle {
  width: 40px;
  height: 40px;
  border-radius: 6px;
  margin: 2px;
  @include flexed(column, center, center);

  border: 1px solid $line-lighter;
}

.green {
  background-color: $green;
}

.red {
  background-color: $red;
}

.primary {
  background-color: $primary;
}

.flat {
  background-color: $secondary;
}

.mix {
  background-color: $secondary;
  animation: flash 0.8s ease-in-out infinite alternate-reverse;
  @keyframes flash {
    from {
      background-color: $secondary;
    }
    to {
      background-color: $hot;
    }
  }
}
</style>
