import axios from "axios";
import tools from "../helpers/tools";

const state = {
  contracts: [],
};

const getters = {
  get_contacts: (state) => {
    if (!state.contracts.length) return [];
    return state.contracts;
  },
};

const actions = {
  async act_create_contract({ commit }, newContract) {
    try {
      const item = await axios.post(
        `${process.env.VUE_APP_BACKEND}/contracts`,
        newContract
      );
      if (!item) throw "There is no data.";
      commit("SET_CONTRACT", item.data);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_get_all_Contracts({ commit }) {
    try {
      const item = await axios.get(`${process.env.VUE_APP_BACKEND}/contracts`);
      if (!item) throw "There is no data.";
      commit("SET_CONTRACTS", tools.groupContractValues(item.data));
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_remove_contract({ commit }, id) {
    try {
      const item = await axios.delete(
        `${process.env.VUE_APP_BACKEND}/contracts/${id}`
      );
      if (!item) throw "There is no data.";

      commit("REMOVE_CONTRACTS", item.data);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },
};

const mutations = {
  SET_CONTRACT: (state, data) => state.contracts.push(data),
  SET_CONTRACTS: (state, data) => (state.contracts = data),
  REMOVE_CONTRACTS: (state, id) =>
    (state.contracts = state.contracts.filter((contract) => contract.id != id)),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
