<template>
  <div class="create-contract-container">
    <list-title :title="lang.createContract" :hint="lang.createContractHint" />
    <round-info :round="get_grouped_round" />
    <hr />
    <list-title :title="lang.findBuyer" />
    <search-buyer @onSubmit="createContract" @closeMe="$emit('closeMe')" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import ListTitle from "../../ListTitle.vue";
import RoundInfo from "./RoundInfo.vue";
import SearchBuyer from "./searchBuyer.vue";
export default {
  components: { RoundInfo, ListTitle, SearchBuyer },
  name: "CreateContractWin",
  computed: {
    ...mapGetters(["get_grouped_round"]),
  },
  methods: {
    ...mapActions(["act_create_contract"]),
    async createContract(e) {
      try {
        const { buyerId, payments } = e;
        const round = JSON.parse(JSON.stringify(this.get_grouped_round));

        const relate_payments = [];
        payments.forEach((pay) => {
          relate_payments.push({ payment: pay });
        });

        const newContract = {
          metter_price: round.metter_price,
          items_count: round.metter_price,
          round: round.id,
          buyer: buyerId,
          relateContractsPayments: relate_payments,
        };

        const result = await this.act_create_contract(newContract);
        if (!result) throw "error creating contract";
        this.$toast.success(this.lang.successProcess);
        this.$emit("closeMe");
      } catch (error) {
        this.$toast.error(this.lang.failedProcess);
        console.error(error);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.create-contract-container {
  max-width: 1000px;
  hr {
    border: 1px solid $line-lighter;
  }
}
</style>