<template>
  <div class="settings-container">
    <list-title :title="lang.settings" />
    <br />
    <form @submit.prevent="saveValue" class="settings-form">
      <div class="inputGroup">
        <label for="eastate_tax">{{ lang.estate_tax + "%" }}</label>
        <InputField
          name="eastate_tax"
          placeholder="value"
          type="number"
          :max="100"
          :min="0"
          :value="estate_tax"
          v-model="estate_tax"
        />
      </div>
      <div class="inputGroup">
        <label for="vat_tax">{{ lang.vat_tax + "%" }}</label>
        <InputField
          name="vat_tax"
          placeholder="value"
          type="number"
          :max="100"
          :min="0"
          :value="vat_tax"
          v-model="vat_tax"
        />
      </div>
      <div class="inputGroup">
        <label for="commission">{{ lang.commission + "%" }}</label>
        <InputField
          name="commission"
          placeholder="value"
          type="number"
          :max="100"
          :min="0"
          step=".01"
          :value="commission"
          v-model="commission"
        />
      </div>
      <div class="btn-group">
        <Button color="green" icon="save" :text="lang.save" type="submit" />
        <Button
          color="red"
          icon="cancel"
          :text="lang.cancel"
          type="button"
          :onClick="cancel"
        />
      </div>
    </form>
  </div>
</template>

<script>
import { mapFields } from "vuex-map-fields";
import { mapActions } from "vuex";
import Button from "../buttons/Button.vue";
import InputField from "../inputs/InputField.vue";
import ListTitle from "../ListTitle.vue";

export default {
  name: "Settings",
  components: { InputField, Button, ListTitle },
  methods: {
    ...mapActions(["act_get_settings", "act_update_settings"]),
    async saveValue() {
      const result = await this.act_update_settings();
      if (!result.state) return this.$toast.error(this.lang.failedProcess);
      this.$toast.success(this.lang.successProcess);
      this.cancel();
    },
    cancel() {
      this.$emit("closeMe");
    },
  },
  computed: {
    ...mapFields([
      "settings.estate_tax",
      "settings.commission",
      "settings.vat_tax",
    ]),
  },
  created() {
    this.act_get_settings();
  },
};
</script>

<style lang="scss" scoped>
.settings-container {
  @include flexed(column, space-between, fill);
  width: auto;
  .settings-form {
    width: auto;
    .inputGroup {
      min-width: 300px;
      text-align: right;
      display: grid;
      grid-template-columns: 30% 70%;
      margin-bottom: 10px;
    }
    .btn-group {
      border-top: 1px solid $line-lighter;
      @include flexed(row, space-between, center);
      margin: 30px 0 0 0;
      padding-top: 20px;
    }
  }
}
</style>
