import axios from "axios";
import tools from "../helpers/tools";

const state = {
  round: {},
};

const getters = {
  get_grouped_round: (state) => {
    return state.round;
  },
  get_round: (state, getters) => {
    state.round.metter_price =
      state.round.step_value *
        (state.round.steps_count > 0 ? state.round.steps_count : 0) +
      state.round.start_value;
    state.round.total_value = state.round.metter_price * state.round.land_area;
    state.round.estate_tax =
      state.round.total_value * (getters.all_settings.estate_tax / 100);
    state.round.commission =
      state.round.total_value * (getters.all_settings.commission / 100);
    state.round.totalPrice = state.round.total_value + state.round.commission;
    return state.round;
  },

  round_show_data: (state, getters) => {
    state.round.metter_price =
      state.round.start_value +
      state.round.steps_count * state.round.step_value;
    state.round.total_value = state.round.metter_price * state.round.land_areas;
    state.round.estate_tax =
      state.round.total_value * (getters.all_settings.estate_tax / 100);
    state.round.commission =
      state.round.total_value * (getters.all_settings.commission / 100);
    state.round.total_price = state.round.total_value + state.round.commission;
    return state.round;
  },
};

const actions = {
  async act_get_round({ commit }, id) {
    try {
      const foundRound = await axios.get(
        `${process.env.VUE_APP_BACKEND}/rounds?id=${id}`
      );

      commit("SET_ROUND", tools.groupRoundValues(foundRound.data)[0]);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_get_show_round({ commit }) {
    try {
      const foundRound = await axios.get(
        `${process.env.VUE_APP_BACKEND}/rounds?is_finish=0&status=1`
      );
      if (!foundRound.data.length)
        commit("SET_ROUND", tools.groupRoundValues(foundRound.data)[0]);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  act_up_step({ commit }) {
    commit("SET_UP_STEP");
  },
  act_down_step({ commit }) {
    commit("SET_DOWN_STEP");
  },

  act_clear_round({ commit }) {
    commit("CLEAR_ROUND");
  },
};

const mutations = {
  SET_ROUND: (state, round) => (state.round = round),
  CLEAR_ROUND: (state) => {
    for (const elm in state.round) state.round[elm] = null;
  },
  SET_UP_STEP: (state) => state.round.steps_count++,
  SET_DOWN_STEP: (state) =>
    state.round.steps_count > 0
      ? state.round.steps_count--
      : state.round.steps_count,
};

export default {
  state,
  getters,
  actions,
  mutations,
};
