import axios from "axios";
import tools from "../helpers/tools";

const state = {
  rounds: [],
};

const getters = {
  all_rounds: (state) => tools.groupRoundValues(state.rounds),
};

const actions = {
  async act_get_all_rounds({ commit }) {
    try {
      const foundItem = await axios.get(
        `${process.env.VUE_APP_BACKEND}/rounds?is_finish=false`
      );

      if (!foundItem) throw "There is no data.";
      commit("add_all_rounds", foundItem.data);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_create_new_round({ commit, dispatch }, groupInfo) {
    console.log("foundITem", groupInfo);
    try {
      const foundItem = await axios.post(
        `${process.env.VUE_APP_BACKEND}/rounds`,
        groupInfo
      );

      if (!foundItem) throw "There is no data.";
      commit("add_round", foundItem.data);
      dispatch("act_get_all_rounds");
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_remove_round({ commit, dispatch }, id) {
    try {
      await axios.delete(`${process.env.VUE_APP_BACKEND}/rounds/${id}`);
      commit("remove_round", id);
      dispatch("act_requestAllItems", {}, { root: true });
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_update_round(context, data) {
    console.log(data.values);
    try {
      await axios.patch(
        `${process.env.VUE_APP_BACKEND}/rounds/${data.id}`,
        data.values
      );
      context.dispatch("act_get_all_rounds");
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  },
};

const mutations = {
  add_all_rounds: (state, data) => (state.rounds = data),
  add_round: (state, data) => state.rounds.push(data),
  remove_round: (state, id) =>
    (state.rounds = state.rounds.filter((round) => round.id != id)),
};

export default {
  state,
  getters,
  actions,
  mutations,
};
