<template>
  <table class="list-table">
    <thead>
      <slot name="head"></slot>
    </thead>
    <tbody v-if="isResults">
      <slot name="body"></slot>
    </tbody>
    <tbody v-else>
      <tr>
        <td class="no-result-msg" colspan="3">{{ words.noResults }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: "TableList",
  data() {
    return {
      words: this.lang,
    };
  },
  props: {
    isResults: Boolean,
  },
};
</script>

<style lang="scss">
.active_input {
  border: 1px;
  background: transparent;
  &:focus {
    outline: 1px solid $secondary;
  }
}
.dis_active {
  border: 0px;
  background: transparent;
  &:focus {
    outline: unset;
  }
}
.list-table {
  width: 100%;
  border-radius: $radius;
  border-spacing: 0;
  .no-result-msg {
    padding: 20px;
    text-align: right;
  }
  thead {
    width: calc(100% - 1rem);
    tr {
      display: table;
      width: 100%;
      table-layout: fixed;
      th {
        background-color: $primary;
        &:first-child {
          width: 50px;
          background-color: rgb(238, 238, 238);
          color: $dark-text;
        }
        &:last-child {
          border-top-left-radius: $radius;
        }
      }

      color: $light-text;
      height: 40px;
    }
  }

  tbody {
    display: block;
    max-height: 50vh;
    overflow-y: scroll;
    @include scrollBar();

    tr {
      display: table;
      width: 100%;
      table-layout: fixed;
      cursor: pointer;
      transition: 0.5s ease-out;
      &:focus {
        background-color: $green;
        font-weight: 700;
      }
      &:hover {
        background-color: $secondary;

        font-size: 1.05rem;
        font-weight: 700;
      }
      td {
        border-bottom: 1px solid $line-lighter;
        padding: 15px 0;
        text-align: center;

        &:first-child {
          width: 50px;
          border-left: 1px solid $line-lighter;
          background-color: rgb(238, 238, 238);
        }
      }

      &:last-child {
        td {
          border-bottom: none;
        }
      }
    }
  }
}
</style>
