<template>
  <form class="search-from" @submit.prevent="$emit('searchValue', searchValue)">
    <InputField
      type="number"
      v-model="searchValue"
      :placeholder="placeHolder"
      @onChange="$emit('onChange', $event)"
    />
    <Button color="green" icon="find" type="submit" shape="circle" />
  </form>
</template>

<script>
import Button from "../buttons/Button.vue";
import InputField from "../inputs/InputField.vue";

export default {
  name: "search",
  components: { InputField, Button },
  props: ["placeHolder"],
  data() {
    return {
      lang: this.lang,
      searchValue: "",
    };
  },
};
</script>

<style lang="scss" scoped>
.search-from {
  width: 100%;
  @include flexed(row, space-between, center);
}
</style>
