import axios from "axios";
import { createHelpers } from "vuex-map-fields";

// Because by default, getters and mutations in Vuex
// modules, are globally accessible and not namespaced,
// you most likely want to rename the getter and mutation
// helpers because otherwise you can't reuse them in multiple,
// non namespaced modules.
const { getItemFields, updateItemFields } = createHelpers({
  getterType: "getItemFields",
  mutationType: "updateItemFields",
});

const state = {
  item: {},
};

const getters = {
  getItemFields,
};

const actions = {
  async act_get_item({ commit }, id) {
    try {
      const item = await axios.get(
        `${process.env.VUE_APP_BACKEND}/items?id=${id}`
      );
      if (!item) throw "There is no data.";
      commit("set_item", item.data[0]);
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },

  async act_update_item({ commit, dispatch }) {
    try {
      const values = state.item;
      const updated = await axios.patch(
        `${process.env.VUE_APP_BACKEND}/items/${values.id}`,
        values
      );
      if (updated.status != (200 || 201)) {
        throw "خطا في ادخال البيانات";
      }
      await dispatch("act_requestAllItems", {}, { root: true });
      commit("clear_item");
      return true;
    } catch (error) {
      console.log(error);
      return false;
    }
  },
};

const mutations = {
  set_item: (state, data) => (state.item = data),
  clear_item: (state) => (state.item = {}),
  updateItemFields,
};

export default {
  state,
  getters,
  actions,
  mutations,
};
