<template>
  <div class="items-list-view">
    <div class="table-container">
      <div class="title-container">
        <list-title :hint="lang.itemsListHint" :title="lang.itemsList" />
      </div>
      <TableList :isResults="items.length > 0">
        <template v-if="items.length > 0" v-slot:head>
          <tr>
            <th>{{ words.serial }}</th>
            <th>{{ words.item_no }}</th>
            <th>{{ words.land_area }}</th>
            <th></th>
          </tr>
        </template>

        <template v-slot:body>
          <tr v-for="(item, key) in items" :key="item.id">
            <td>{{ key + 1 }}</td>
            <td>{{ item.item_no }}</td>
            <td>{{ item.land_area }}</td>
            <td>
              <ListIcons
                :itemId="item.id"
                :trashIcon="false"
                :editIcon="false"
                :addToListIcon="checked ? false : true"
                @addToList="addToList"
              />
            </td>
          </tr>
        </template>
      </TableList>
    </div>
    <div class="table-footer">
      <small>{{ `${words.itemsCount} ${items.length}` }}</small>
    </div>
  </div>
</template>

<script>
import ListIcons from "../icons/ListIcons.vue";
import { mapActions } from "vuex";
import TableList from "../TableList.vue";
import ListTitle from "../ListTitle.vue";

export default {
  components: { ListIcons, TableList, ListTitle },
  name: "ItemsList",
  data() {
    return {
      words: this.lang,
      checked: false,
    };
  },
  props: {
    title: String,
    hint: String,
    items: Array,
  },
  methods: {
    ...mapActions([
      "act_removeOne",
      "act_add_item_to_group",
      "act_remove_item_from_items",
    ]),
    async trash(e) {
      try {
        if (!e) throw "There is no value to remove!";
        const result = await this.act_removeOne(e);
        if (!result) throw "no success";
        this.$toast.success("ok");
      } catch (error) {
        this.$toast.error(error);
        console.log(error);
      }
    },
    async addToList(id) {
      try {
        const results = await this.act_add_item_to_group(id);
        if (!results) throw "no success";
      } catch (error) {
        this.$toast.error(error);
        console.log(error);
      }
    },
    editItem() {},
  },
};
</script>

<style lang="scss" scoped>
.items-list-view {
  min-width: 400px;
  .table-container {
    @include flexed(column, flex-start, flex-start);
    .title-container {
      width: 100%;
      @include flexed(row, space-between, center);
      .checkbox-container {
        width: 50%;
        text-align: left;
      }
      .itemListTitle {
        width: 50%;
        text-align: right;
        margin: 0 0 5px 0;
        h3 {
          margin-bottom: 1px;
        }
      }
    }
  }
  .table-footer {
    text-align: right;

    border-top: 1px solid $line-lighter;
    padding: 5px 5px;
    margin: 20px 0;
  }
}
</style>
