<template>
  <div class="group-list-view">
    <div class="table-container">
      <div class="title-container">
        <list-title :hint="lang.itemsListHint" :title="lang.itemsList" />
      </div>

      <TableList :isResults="getGroupItems.length > 0">
        <template v-slot:head>
          <tr>
            <th>{{ words.serial }}</th>
            <th>{{ words.item_no }}</th>
            <th>{{ words.land_area }}</th>
            <th></th>
          </tr>
        </template>
        <template v-slot:body>
          <tr v-for="(item, key) in getGroupItems" :key="item.id">
            <td>{{ key + 1 }}</td>
            <td>{{ item.item_no }}</td>
            <td>{{ item.land_area }}</td>
            <td>
              <ListIcons
                :itemId="item.id"
                :trashIcon="true"
                :editIcon="false"
                :addToListIcon="false"
                @trash="trash"
              />
            </td>
          </tr>
        </template>
      </TableList>
    </div>
    <div class="table-footer">
      <small>{{ `${words.itemsCount} ${getGroupItems.length}` }}</small>
    </div>

    <div v-show="getGroupItems.length > 0" class="createBid-container">
      <CreateBid @values="createRound" @cancel="cancel" />
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CreateBid from "../forms/CreateBid.vue";
import ListIcons from "../icons/ListIcons.vue";
import ListTitle from "../ListTitle.vue";
import TableList from "../TableList.vue";

export default {
  components: { ListIcons, CreateBid, TableList, ListTitle },
  name: "GroupingItemsList",
  data() {
    return {
      words: this.lang,
    };
  },
  props: {
    title: String,
    hint: String,
  },
  computed: {
    ...mapGetters(["getGroupItems"]),
  },
  methods: {
    ...mapActions([
      "act_rm_item_from_group",
      "act_create_new_round",
      "act_rm_all_from_group",
    ]),
    async trash(id) {
      this.act_rm_item_from_group(id);
    },
    cancel() {
      this.act_rm_all_from_group();
    },
    async createRound(e) {
      try {
        const group = JSON.parse(JSON.stringify(this.getGroupItems));
        const item = [];
        group.forEach((i) => {
          const hold = {};
          hold.item = i;
          item.push(hold);
        });
        if (!e) throw "There is no value to insert!";
        e.relateRoundsItem = item;
        const insertedRound = await this.act_create_new_round(e);
        if (!insertedRound) throw "no success";
        this.$toast.success("ok");
        this.act_rm_all_from_group();
      } catch (error) {
        this.$toast.error(error);
        console.log(error);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.group-list-view {
  width: auto;
  .table-container {
    @include flexed(column, flex-start, flex-start);
    .title-container {
      width: 100%;
      @include flexed(row, space-between, center);
      .checkbox-container {
        width: 50%;
        text-align: left;
      }
      .itemListTitle {
        width: 50%;
        text-align: right;
        margin: 0 0 5px 0;
        h3 {
          margin-bottom: 1px;
        }
      }
    }
  }
  .table-footer {
    text-align: right;

    border-top: 1px solid $line-lighter;
    padding: 5px 5px;
    margin: 20px 0;
  }
}
</style>
