<template>
  <div class="welcome-body">
    <h2>Welcome {{ get_user.username || get_user.fullname }}</h2>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "welcome",
  data() {
    return {
      timer: 5,
    };
  },
  computed: {
    ...mapGetters(["get_user"]),
  },
  created() {
    setTimeout(() => {
      this.$router.push({ name: "main" });
    }, 1000);
  },
};
</script>

