import { createRouter, createWebHashHistory } from "vue-router";

import Main from "../views/cpanel/Main.vue";
import MainPage from "../views/cpanel/MainPage.vue";
import Import from "../views/cpanel/Import.vue";
import ItemEdit from "../views/cpanel/ItemEdit.vue";
import Items from "../views/cpanel/items/Items.vue";

import Buyers from "../views/cpanel/buyers/Buyers.vue";
import ShowScreen from "../views/display/ShowScreen.vue";
import Login from "../views/auth/Login.vue";
import Logout from "../views/auth/Logout.vue";
import Welcome from "../views/auth/Welcome.vue";
import Home from "../views/Home.vue";
import tokenStore from "../helpers/localStorage/authTokens";

const routes = [
  {
    path: "/cpanel",
    name: "main",
    component: Main,
    children: [
      {
        path: "",
        name: "MainPage",
        component: MainPage,
      },
      {
        path: "/cpanel/items",
        name: "Items",
        component: Items,
      },
      {
        path: "/cpanel/import",
        name: "Import",
        component: Import,
      },

      {
        path: "/cpanel/buyers",
        name: "Buyers",
        component: Buyers,
      },
      {
        path: "/cpanel/itemEdit/:id",
        name: "ItemEdit",
        component: ItemEdit,
      },
    ],
  },

  {
    path: "/showScreen",
    name: "ShowScreen",
    component: ShowScreen,
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
  },

  {
    path: "/logout",
    name: "Logout",
    component: Logout,
  },
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/welcome",
    name: "Welcome",
    component: Welcome,
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  { path: "/:catchAll(.*)", component: Home },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.name !== "Login" && !tokenStore.get()) next({ name: "Login" });
  else next();
});

export default router;
