<template>
  <div>
    <h3>{{ words.import }}</h3>
    <Pad>
      <ImportFile @getFile="getFiles" />

      <table>
        <thead>
          <tr>
            <th><input-field type="checkbox" /></th>
            <th>م</th>
            <th>الرقم</th>
            <th>المساحة</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, key) in results" :key="item" :id="item.i_number">
            <td>
              <input
                type="checkbox"
                :id="item.i_number"
                :value="item.i_number"
                v-model="checked"
                @change="checkboxChange"
              />
            </td>
            <td>{{ key + 1 }}</td>
            <td>{{ item.i_number }}</td>
            <td>{{ item.i_land_area }}</td>
          </tr>
        </tbody>
      </table>
    </Pad>
  </div>
</template>

<script>
import ImportFile from "../../components/forms/ImportFile.vue";
import Pad from "../../components/layouts/Pad.vue";
import Papa from "papaparse";
import InputField from "../../components/inputs/InputField.vue";

function getParse(ts, files) {
  if (files) {
    Papa.parse(files[0], {
      header: true,
      skipEmptyLines: true,
      complete: function (rs) {
        ts.results = rs.data;
        console.log(ts.results);
      }.bind(ts),
    });
  }
}

//FIXME: complate this and read file and convert it to database.
export default {
  components: { ImportFile, Pad, InputField },
  name: "Import",
  data() {
    return {
      words: this.lang,
      results: [],
      checked: [],
    };
  },
  methods: {
    getFiles(files) {
      getParse(this, files);
    },
    checkboxChange() {
      console.log(this.checked);
    },
  },
};
</script>

