import wordsLs from "./words.json";

import LStore from "../helpers/localStorage/lsClass";

// define
const lsLanguage = new LStore("lang");

const ls = {};

ls.language = lsLanguage;

const wordsList = wordsLs;

function getWords(lang = ls.language.get()) {
  const result = {};
  for (const key in wordsList) {
    result[key] = wordsList[key][lang];
  }
  return result;
}

if (!ls.language.get()) ls.language.set("ar");

export default getWords();
