<template>
  <form
    class="login-form"
    @submit.prevent="$emit('onSubmit', { username, password })"
  >
    <div class="form-group">
      <label> {{ lang.username }} </label>
      <input type="text" class="input-field" v-model="username" required />
    </div>
    <div class="form-group">
      <label> {{ lang.password }} </label>
      <input type="password" class="input-field" v-model="password" required />
    </div>

    <Button type="submit" :text="lang.login" />
  </form>
</template>

<script>
import Button from "../buttons/Button.vue";
export default {
  components: { Button },
  name: "LoginForm",
  data() {
    return {
      username: "",
      password: "",
    };
  },
};
</script>

<style lang="scss" scoped>
.login-form {
  width: 100%;
  padding: 30px;
  background-color: $line-lighter;
  border-radius: $radius;
  border-top-right-radius: 0;
  @include flexed(column, space-between, center);
  .form-group {
    min-width: 400px;
    margin: 10px 0;
    display: grid;
    grid-template-columns: 100px auto;
    align-items: center;
    .input-field {
      @include inputs();
      width: auto;
    }
  }
}
</style>