<template>
  <div class="item-list-win-Container">
    <div class="searchBar">
      <Search @searchValue="search" @onChange="clearIfEmpty" />
    </div>
    <ItemsList :items="getAllItems" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Search from "../forms/searchForm.vue";
import ItemsList from "../lists/ItemsList.vue";

export default {
  name: "ItemListWin",
  components: { ItemsList, Search },
  data() {
    return {
      lang: this.lang,
      showAllItems: false,
    };
  },
  methods: {
    ...mapActions(["act_get_available_items", "act_search"]),
    toggleShowAllItems() {
      this.showAllItems = !this.showAllItems;
    },
    async search(e) {
      try {
        const result = await this.act_search(e);
        if (!result) this.$toast.error(this.lang.noResults);
      } catch (error) {
        console.error(error);
        this.$toast.error(this.lang.noResults);
      }
    },
    async clearIfEmpty(e) {
      if (e != "") return;
      try {
        const result = await this.act_search(e);
        if (!result) this.$toast.error(this.lang.noResults);
      } catch (error) {
        console.error(error);
        this.$toast.error(this.lang.noResults);
      }
    },
  },
  computed: {
    ...mapGetters(["getAllItems", "all_Items_get"]),
  },
  created() {
    try {
      this.act_get_available_items();
      this.$toast.info(this.lang.requestDone);
    } catch (error) {
      this.$toast.error(this.lang.requestDataError);
    }
  },
};
</script>

<style lang="scss" scoped>
.item-list-win-Container {
  @include flexed(column, flex-start, flex-start);
}
</style>
